import './App.css';

import React from 'react';
import Sound from 'react-sound';

const touches = [
    {
      soundURL : "coq.mp3",
      name : "Coq"
    },
    {
      soundURL : "chèvre.mp3",
      name : "Chèvre"
    },
    {
      soundURL : "klaxon.mp3",
      name : "Klaxon"
    }
  ];

class Touche extends React.Component {

  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.handleSongFinishedPlaying = this.handleSongFinishedPlaying(this);
    this.state = { playStatus : Sound.status.STOPPED }
  }

  handleClick() {
    this.setState( {playStatus: Sound.status.PLAYING} );
  }

  handleSongFinishedPlaying() {
    this.setState( {playStatus: Sound.status.STOPPED} )
  }

  render() {
    return (
    <React.Fragment>
      <button onClick={this.handleClick}>{this.props.name}</button>
      <Sound url={process.env.PUBLIC_URL + '/' + this.props.soundURL}
        playStatus={this.state.playStatus}
        onFinishedPlaying={this.handleSongFinishedPlaying}/>
    </React.Fragment>
    );
  }
}

class App extends React.Component {
  
    render() {
    const listeTouches = touches.map ( (touche) =>
      <Touche name={touche.name} key={touche.name} soundURL={touche.soundURL}/>);
    return (
      <div className="App">
        {listeTouches}
      </div>
      );
    }
}

export default App;
